.searchBar {
  display: flex;
  background-color: #fff;
  padding: 20px;
  border: 2px solid #c9c2c2;
  flex-direction: column;
}
.searchBar__container {
  display: flex;
  border: 2px solid #c9c2c2;
  height: 45px;
}

.searchBar__input {
  width: 100%;
  border: none;
  font-size: 16px;
}

.searchBar__input:focus {
  border: none;
}

.searchBar__btn {
  background: transparent;
  border-style: none;
  color: #878181;
}

.searchBar__error {
  color: red;
}
